#homepage {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}

@-webkit-keyframes pointer-shrink {
    0% {
        height: 150px;
    }

    50% {
        height: 20px;
    }

    75% {
        height: 10px;
    }

    100% {
        height: 0px;
    }
}

@keyframes pointer-shrink {
    0% {
        height: 150px;
    }

    50% {
        height: 20px;
    }

    75% {
        height: 10px;
    }

    100% {
        height: 0px;
    }
}

@-webkit-keyframes pointer-move {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    50% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }

    100% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
}

@keyframes pointer-move {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    50% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }

    100% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
}

.vertical-pointer-container {
    width: 10px;
    height: 150px;
    overflow: hidden;

    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;


    background: none;
}

.vertical-pointer {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    width: 100%;
    height: 100%;

    -webkit-transform: translateY(-100%);

        -ms-transform: translateY(-100%);

            transform: translateY(-100%);
    background: none;

    border-style: none solid none solid;
    border-width: 2px;
    border-color: var(--accent-green-two);
    /*    
   */

    -webkit-animation-name: pointer-move;

            animation-name: pointer-move;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

#home-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-width: 600px;
    width: 100%;
    min-height: 400px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;

    position: relative;
    padding: 2rem 0;
    
    background-image: url('../backgrounds/z-logo.svg');
    background-repeat: no-repeat;
    background-position: right center;
    background-size: contain;

    color: var(--home-text-fg);
}

#home-info-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;

    padding: 2rem 0;
}

.home-info {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    
    margin: 0;
    padding: 0;
}

span[id='name'] {
    font-size: var(--home-header-size);
    line-height: var(--home-header-line-height);
    font-weight: bold;
}

#homepage-effect {
    font-size: var(--home-effect-size);
    line-height: var(--home-effect-line-height);
}

#about-me-btn {    
    position: absolute;
    bottom: -1.5rem;
    left: 0;
}



