@-webkit-keyframes nav-stretch-in {
    from {
        width: 0%;
    }

    to {
        width: 100%;  
    }
}

@keyframes nav-stretch-in {
    from {
        width: 0%;
    }

    to {
        width: 100%;  
    }
}

@-webkit-keyframes nav-slide-down {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes nav-slide-down {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@-webkit-keyframes nav-collapse {
    0% {
        width: 100%;
        border-radius: 0;
        -webkit-transform: translate(-60%, 15%);
                transform: translate(-60%, 15%);
    }

    75% {
        border-radius: 0;
        -webkit-transform: translate(-60%, 15%);
                transform: translate(-60%, 15%);
    }

    100% {
        width: var(--navbar-height);
        border-radius: 0.75rem;
        -webkit-transform: translate(-60%, 15%);
                transform: translate(-60%, 15%);
    }
}

@keyframes nav-collapse {
    0% {
        width: 100%;
        border-radius: 0;
        -webkit-transform: translate(-60%, 15%);
                transform: translate(-60%, 15%);
    }

    75% {
        border-radius: 0;
        -webkit-transform: translate(-60%, 15%);
                transform: translate(-60%, 15%);
    }

    100% {
        width: var(--navbar-height);
        border-radius: 0.75rem;
        -webkit-transform: translate(-60%, 15%);
                transform: translate(-60%, 15%);
    }
}

.navbar-container {
    --navbar-height: 3rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    
    width: 100%;
    min-height: var(--navbar-height);
    height: var(--navbar-height);

    z-index: 2;

    position: absolute;
    bottom: -3rem;
    right: 0;

    margin: 0;
    padding: 0 1.6rem;
    overflow: hidden;

    border-style: none none solid none;
    border-width: 1px;
    border-color: black;
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
  
    background: var(--blackish);

    -webkit-transition: all 0.35s;

    -o-transition: all 0.35s;

    transition: all 0.35s;
}

.navbar-container.sticky {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: fixed;
    top: 0;
    right: 0;
    width: var(--navbar-height);
    height: var(--navbar-height);

    padding: 1.6rem;

    -webkit-transform: translate(-60%, 15%);

        -ms-transform: translate(-60%, 15%);

            transform: translate(-60%, 15%);
    border-radius: 0.75rem;
    -webkit-box-shadow: 1px 2px 5px black;
            box-shadow: 1px 2px 5px black;

    -webkit-animation-name: nav-collapse;

            animation-name: nav-collapse;
    -webkit-animation-duration: 0.35s;
            animation-duration: 0.35s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;

    -webkit-transition: -webkit-transform 0.25s;

    transition: -webkit-transform 0.25s;

    -o-transition: transform 0.25s;

    transition: transform 0.25s;

    transition: transform 0.25s, -webkit-transform 0.25s;
}

.navbar-container.sticky:hover {
    -webkit-transform: scale(1.05, 1.05) translate(-60%, 15%);
        -ms-transform: scale(1.05, 1.05) translate(-60%, 15%);
            transform: scale(1.05, 1.05) translate(-60%, 15%);
}

.icon-text {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    margin: 0rem 1rem;
    padding: 0.1rem 0.5rem;
    
    text-align: center;
    font-size: 1.3em;
    font-weight: bold;

    font-family: 'Courier New', Courier, monospace;
    color: white;
    text-shadow: 1px 1px 5px black;
}

.icon-text:hover {
    color: var(--accent-green-two);    
}

.icon-text.selected {
    position: relative;

    color: var(--accent-green-two);
}

@-webkit-keyframes selected-underline-grow {
    from {
        width: 0%;
    }

    to {
        width: 100%;
    }
}

@keyframes selected-underline-grow {
    from {
        width: 0%;
    }

    to {
        width: 100%;
    }
}

.icon-text.selected::after {
    content: '';
    display: inline-block;

    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;

    background: none;
    border-style: none none solid none;
    border-width: 1px;
    border-color: var(--accent-green-two);

    -webkit-animation-name: selected-underline-grow;

            animation-name: selected-underline-grow;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

.icon-text.desktop-only {
    display: none;
}

.icon-text.mobile-only {
    margin: 0;
    padding: 0;

    display: block;
}

.navbar-icon {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 2.25rem;
    height: 2.25rem;
    
    display: block;
    margin: 0;
    padding: 0.25rem;

    -webkit-filter: var(--navbar-icon-filter);

            filter: var(--navbar-icon-filter);
}

#menu {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;

    margin: 0;
    padding: 0;

    display: block;
}

#menu-img {
    display: block;
    
    width: 2rem;
    height: 2rem;
    padding: 0;
    -webkit-transform: translateX(50%);
        -ms-transform: translateX(50%);
            transform: translateX(50%);
}

@media only screen and (min-width: 900px) {
    .icon-text.desktop-only {
        display: -ms-flexbox;
        display: flex;   
    }
    
    #menu {
        display: none;
    }

    .navbar-container.sticky .icon-text.desktop-only {
        display: none;
    }

    .navbar-container.sticky #menu {
        display: block;
    }

    /*
    .navbar-container.sticky {
